import axios from 'axios'
import qs from 'qs'
import { geojsonToArcGIS } from '@esri/arcgis-to-geojson-utils';

export async function getToken (user, password) {
  const host = window.location.host
  const data = {
    client: 'referer',
    expiration: 720,
    f: 'json',
    referer: host,
    password: password,
    username: user
  }
  const options = {
    method: 'POST',
    headers: { 'content-type': 'application/x-www-form-urlencoded' },
    url: `https://gis.msf.org/arcgis/tokens/generateToken`,
    data: qs.stringify(data)
  }
  const res = await axios(options)
  if (res.status !== 200) {
    throw new Error('Network Failure')
  }
  return res.data
}

export async function deleteTable (token){
  const data = {
    objectIds: '',
    where: "1=1",
    f: "json",
    rollbackOnFailure: true,
  }
  const options = {
    method: 'POST',
    headers: { 'content-type': 'application/x-www-form-urlencoded' },
    url: `https://gis.msf.org/arcgis/rest/services/yem/yem_edit/FeatureServer/0/deleteFeatures?token=${token}`,
    data: qs.stringify(data)
  }
  const res = await axios(options)
  return res
}

export async function addData(dataset, token){
  const arcgis = geojsonToArcGIS(dataset);
  const data = {
    features: JSON.stringify(arcgis),
    rollbackOnFailure: true,
    f: 'json',
  }
  const options = {
    method: 'POST',
    headers: { 'content-type': 'application/x-www-form-urlencoded' },
    url: `https://gis.msf.org/arcgis/rest/services/yem/yem_edit/FeatureServer/0/addFeatures?token=${token}`,
    data: qs.stringify(data)
  }
  const res = await axios(options)
  return res
}
