import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios'
import qs from 'qs'
import L from 'leaflet'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    layerDialog: false,
    dataDialog: false,
    loginDialog: false,
    adminlogged: false,
    token: null,
    buildingLayer: null,
    mgrsList: null
  },
  mutations: {
    switchLayerDialog (state) {
      state.layerDialog ? state.layerDialog = false : state.layerDialog = true
    },
    switchDataDialog (state) {
      state.dataDialog ? state.dataDialog = false : state.dataDialog = true
    },
    switchLoginDialog (state) {
      state.loginDialog ? state.loginDialog = false : state.loginDialog = true
    },
    setBuildings(state, dataset){
      state.buildingLayer = dataset
    },
    setMGRS(state, dataset){
      state.mgrsList = dataset
    },
    setToken (state, token) {
      state.adminlogged = true
      state.token = token
    }
  },
  actions: {
    getData(context){
      axios.get(`https://gis.msf.org/arcgis/rest/services/yem/yem_view/MapServer/0/query?where=1%3D1&text=&objectIds=&time=&geometry=&geometryType=esriGeometryEnvelope&inSR=&spatialRel=esriSpatialRelIntersects&relationParam=&outFields=*&returnGeometry=true&returnTrueCurves=false&maxAllowableOffset=&geometryPrecision=&outSR=&having=&returnIdsOnly=false&returnCountOnly=false&orderByFields=&groupByFieldsForStatistics=&outStatistics=&returnZ=false&returnM=false&gdbVersion=&historicMoment=&returnDistinctValues=false&resultOffset=&resultRecordCount=&queryByDistance=&returnExtentOnly=false&datumTransformation=&parameterValues=&rangeValues=&quantizationParameters=&f=geojson`)
      .then(response => {
        const mgrsArray = []
        for (let item of response.data.features) {
          for (let i of item.properties.mgrs.split('-')) {
            mgrsArray.push(i)
          }
        }
        context.commit('setMGRS', mgrsArray)
        context.commit('setBuildings', L.geoJSON(response.data))
      })
    }
  }
});
