<template lang="html">
    <div class="col-md-12 main-container" id="map">
    </div>
</template>

<style lang="css" scoped>
  .main-container{
    height: 100%;
    margin: 0;
    padding: 0;
  }
</style>

<script>
  import L from 'leaflet'

  export default {
    name: "BuildingMap",
    mounted() {
      this.initMap();
      this.$store.dispatch('getData')
    },
    data(){
      return {
        map: null,
        tileLayer: null,
        dataLayer: null
      }
    },
    computed:{
      dataset(){
        return this.$store.state.buildingLayer
      }
    },
    watch:{
      dataset(){
        this.refreshMap()
      }
    },
    methods: {
      initMap() {
        this.map = L.map('map',{zoomControl: false,}).setView([15, 47], 8)
        this.map.addControl(L.control.zoom({ position: 'topright' }))
        this.tileLayer = L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
            attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
            id: 'mapbox.satellite',
            accessToken: 'pk.eyJ1IjoiY2FydG9saXZpZXIiLCJhIjoiY2ptN2F4Y2huMDFqMjNrbW1oM2Z0cmJnayJ9.hgdKRVi2rOaI1LRAq-oj7A'
        })
        this.tileLayer.addTo(this.map)
      },
      refreshMap(){
        if (this.map.hasLayer(this.buildingLayer)) {
          this.map.removeLayer(this.buildingLayer)
        }
        this.buildingLayer = this.dataset

        const oldStyle = {
          "weight": 3,
          "color": "#000",
          "opacity": 0.9,
          "fillColor": "#fff",
          "fillOpacity": 0.1
        };
        const newStyle = {
          "weight": 6,
          "color": "#CF2825",
          "opacity": 0.9,
          "fillColor": "#fff",
          "fillOpacity": 0.1
        };

        this.buildingLayer.eachLayer(layer => {
          const popUpText = `
          <div class="v-data-table theme--light">
          <div class="v-data-table__wrapper">
            <table>
              <tr>
                <th class="text-left">Name</th>
                <td class="text-center">${layer.feature.properties.name}</td>
              </tr>
              <tr>
                <th class="text-left">OC</th>
                <td class="text-center">${layer.feature.properties.oc}</td>
              </tr>
              <tr>
                <th class="text-left">Gov</th>
                <td class="text-center">${layer.feature.properties.gov}</td>
              </tr>
            </table>
          </div>
          </div>`
          if (layer.feature.properties.new_ == 0) {
            layer.setStyle(oldStyle)
          } else if (layer.feature.properties.new_ == 1) {
            layer.setStyle(newStyle)
          }

          layer.bindPopup(popUpText)
        })
        this.buildingLayer.addTo(this.map)

      }
    },
  }
</script>
