<template lang="html">
  <!-- <div class="col-md-12 main-container"> -->
    <!-- <v-row justify="center"> -->
      <v-dialog v-model="dataDialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="#CF2825">
            <v-btn icon dark @click="switchDataDialog">
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Manage Dataset</v-toolbar-title>
            <div class="flex-grow-1"></div>
            <v-toolbar-items>
              <v-btn dark text @click="updateDataset">Save</v-btn>
            </v-toolbar-items>
          </v-toolbar>

          <v-list three-line>
            <!-- <v-subheader>User Controls</v-subheader> -->
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Update Dataset </v-list-item-title>
                <v-file-input label="Add a new dataset" @change="processFile($event)" accept=".csv"></v-file-input>
                <v-list-item-subtitle>This new file will replace the current one. If you are not sure about file quality, please contact your administrator</v-list-item-subtitle>
                <v-card-actions class="justify-center">
                <v-btn
                  color="success"
                  class="mr-4 mt-3 col-2"
                  @click="loadNewDataset"
                > Validate
              </v-btn>
              </v-card-actions>

              <v-alert v-model="csvError" type="error" class="mt-3" dismissible="dismissible">
                {{errorMessage}}
              </v-alert>
              </v-list-item-content>
            </v-list-item>
          </v-list>
          <v-divider></v-divider>
          <div class="text-center mt-3" v-if="newDataset">
            <h2 class="title">New Dataset</h2>
            <v-simple-table
              fixed-header
            >
              <thead>
                <tr>
                  <th class="text-center">Name</th>
                  <th class="text-center">Gov</th>
                  <th class="text-center">OC</th>
                  <th class="text-center">New</th>
                  <th class="text-center">MGRS</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in extractedData" :key="item.name">
                  <td>{{ item[0].Name.substring(0, item[0].Name.length-1) }}</td>
                  <td>{{ item[0].Gov }}</td>
                  <td>
                    <v-chip
                      :style="{'background-color': chipsColor(item[0].Type)}"
                      text-color="white"
                    >
                      {{ item[0].Type }}
                    </v-chip>
                  </td>
                  <td>
                    <v-chip
                      v-if="item[0].new == true"
                      :style="{'background-color': '#4DB6AC'}"
                      text-color="white"
                    >
                      New
                    </v-chip>
                  </td>
                  <td>{{ item[0].MGRS }} - {{ item[1].MGRS }} - {{ item[2].MGRS }} - {{ item[3].MGRS }}</td>
                </tr>
              </tbody>
            </v-simple-table>
          </div>
        </v-card>
      </v-dialog>

    </v-row>
  <!-- </div> -->

</template>

<style lang="css" scoped>
  .main-container{
    height: 100%;
    margin: 0;
    padding: 0;
  }
</style>

<script>
import papa from 'papaparse'
import L from 'leaflet'
import { deleteTable, addData } from '@/services/arcgis'

export default {
  name: 'DataManager',
  data(){
    return {
      csvError:false,
      newDataset: false,
      extractedData: null,
      errorMessage: 'An error has been detected on your file (Only buildings with 4 corners are accepted).'
    }
  },
  computed:{
    dataDialog(){
      return this.$store.state.dataDialog
    },
    mgrsList(){
      return this.$store.state.mgrsList
    },
    token(){
      return this.$store.state.token
    }
  },
  methods:{
    switchDataDialog(){
      this.$store.commit('switchDataDialog')
    },
    processFile(){
      this.someData = event.target.files[0]
    },
    loadNewDataset(){
      papa.parse(this.someData, {
        header: true,
      	complete: (results) => {
          const result = results.data
          const resTable = []
          let temptable = []
          // for (var a of result) {
          //   const temptable = []
          //   if (!isNaN(parseInt(a.No))) {
          //     console.log(a);
          //     for (let b of result) {
          //       if (a.bid == b.bid && !isNaN(parseInt(b.No))) {
          //         temptable.push(b)
          //       }
          //     }
          //     resTable.push(temptable)
          //   }
          // }
          // console.log(temptable);

      		for (let i = 0; i < result.length; i++) {
            // console.log(temptable);
            if (i % 4 == 0 && !isNaN(parseInt(result[i].No))) {
              temptable = []
              temptable.push(result[i]);
            } else if (!isNaN(parseInt(result[i].No))) {
              temptable.push(result[i]);
            }
            if (temptable.length == 3) {
              resTable.push(temptable)
            }
          }

          this.newDataset = true
          for (let res of resTable) {
            for (let i of res) {
              this.mgrsList.includes(i.MGRS) ? i.new = false : i.new = true
            }
            if (res.length < 4 || res[res.length-1].Name.substring(res[res.length-1].Name.length -1) != '4') {
              this.csvError = true
              this.newDataset = false
              return
            }
          }
          if (this.newDataset) {
            console.log(resTable);
            this.extractedData = resTable
          }
      	}
      })
    },
    async updateDataset(){
      this.newDataset = false
      const buildingLayer = L.layerGroup()
      for (let res of this.extractedData) {
        if (res.length < 4 || res[res.length-1].Name.substring(res[res.length-1].Name.length -1) != '4') {
          this.csvError = true
          return
        }
        const polygonPoints = []
        const mgrs = []
        let newItem = 1
        for (let b of res) {
          if (this.mgrsList.includes(b.MGRS)) {
            newItem = 0
          }
          mgrs.push(b.MGRS)
          polygonPoints.push([b.Y, b.X])
        }
        const feature = {
          type: "Feature",
          properties: {
            bid: res[0].bid,
            mgrs: null,
            name: res[0].Name.substring(0, res[0].Name.length-1),
            gov: res[0].Gov,
            oc: res[0].Type,
            new_: newItem,
            update: new Date(Date.now()).toLocaleDateString("fr-FR")
          }
        }
        feature.properties.mgrs = mgrs.join('-')
        const building = L.polygon(polygonPoints).addTo(buildingLayer);
        building.feature = feature
      }
      // const geo = buildingLayer.toGeoJSON()
      // var link = document.createElement('a');
      // link.href = window.URL.createObjectURL(new File([JSON.stringify(geo)], {type: 'text/plain'}))
      // link.download = 'export.geojson';
      // link.click();
      const deleted = await deleteTable(this.token)
      if (deleted.data.success) {
        const added = await addData(buildingLayer.toGeoJSON(), this.token)
        if (added.data.addResults) {
          this.switchDataDialog()
          this.$store.commit('setBuildings', buildingLayer)
        } else {
          this.errorMessage = 'Something went wrong with the update: Insertion Error. Please contact your administrator'
          this.csvError = true
        }
      } else {
        this.errorMessage = 'Something went wrong with the update: Delete Error. Please contact your administrator'
        this.csvError = true
      }
    },
    chipsColor(oc){
      if (oc == 'OCA') {
        return '#E84710'
      } else if (oc == 'OCB') {
        return '#FEC903'
      } else if (oc == 'OCBA') {
       return '#039338'
     } else if (oc == 'OCG') {
       return '#CC0F5C'
     } else if (oc == 'OCP') {
       return '#3A398F'
     } else {
       return '#000'
     }
   },
  }
}
</script>

