<template>
  <v-row justify="center">
    <v-dialog v-model="loginDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Administrator login</span>
          <div class="flex-grow-1"></div>
          <v-btn class="mx-2" fab dark small color="red darken-2" @click="switchLoginDialog">
              <v-icon dark>mdi-close</v-icon>
            </v-btn>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
            <v-alert v-model="errorLogin" type="error" class="mt-3" dismissible="dismissible">
              An error has been detected on your file (Only buildings with 4 corners are accepted).
            </v-alert>
              <v-col cols="12">
                <v-text-field label="Login*" required v-model="loginValue"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="Password*" type="password" required v-model="passwordValue"></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-btn primary large block color="red" @click="login">Login</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { getToken } from '@/services/arcgis'
export default {
  name: 'Login',
  data(){
    return {
      loginValue: null,
      passwordValue: null,
      errorLogin: false
    }
  },
  computed:{
    loginDialog(){
      return this.$store.state.loginDialog
    }
  },
  methods:{
    switchLoginDialog(){
      this.$store.commit('switchLoginDialog')
    },
    async login(){
      const res = await getToken(this.loginValue, this.passwordValue)
      if (res.error) {
        this.errorLogin = true
      } else {
        this.$store.commit('setToken', res.token)
        this.switchLoginDialog()
      }
    }
  }
}
</script>

