<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      app
      clipped
    >
      <v-list dense>
        <v-list-item @click="switchLayerDialog">
          <v-list-item-action>
            <v-icon color="red">mdi-layers-plus</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Add a basemap</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item @click="switchDataDialog">
          <v-list-item-action>
            <v-icon>mdi-database-edit</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Update buildings</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      app
      clipped-left
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>Yemen Deconfliction GeoApp</v-toolbar-title>
      <div class="flex-grow-1"></div>
      <v-btn icon  @click="switchLoginDialog">
        <v-icon>mdi-login-variant</v-icon>
      </v-btn>
    </v-app-bar>
    <div class="modals">

      <v-snackbar
        v-model="snackbarLogin"
        :timeout="timeout"
        left
        color='error'
      >
        You need to be logged as Administrator to manage dataset
        <v-btn
          color="black"
          text
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </v-snackbar>
      <Login/>
      <LayersManager/>
      <DataManager/>
    </div>
    <v-content>
      <v-container
        class="fill-height red lighten-5"
        fluid
      >

      <BuildingMap/>
      </v-container>
    </v-content>

  </v-app>
</template>

<style lang="css" scoped>
  .container{
    margin: 0;
    padding: 0;
  }
  .v-app-bar.v-app-bar--fixed{
    z-index: 1200;
  }
  .modals{
    z-index: 1199
  }
</style>

<script>
  import BuildingMap from './Map.vue'
  import LayersManager from './LayersManager.vue'
  import DataManager from './DataManager.vue'
  import Login from './Login.vue'
  export default {
    components: {
      BuildingMap,
      LayersManager,
      DataManager,
      Login
    },
    data: () => ({
      drawer: true,
      snackbarLogin: false,
      timeout: 2000
    }),
    created () {
      this.$vuetify.theme.dark = false
    },
    computed: {
      adminLogged(){
        return this.$store.state.adminlogged
      }
    },
    methods:{
      switchLayerDialog(){
      this.$store.commit('switchLayerDialog')
      },
      switchDataDialog(){
        this.adminLogged ? this.$store.commit('switchDataDialog') : this.snackbarLogin = true
      },
      switchLoginDialog(){
        this.$store.commit('switchLoginDialog')
      }
    }
  }
</script>
